import React from 'react';
import './App.css';
import AppBar from './components/AppBarWidget';
import {ThemeProvider, createMuiTheme} from '@material-ui/core/styles';

const theme = createMuiTheme({
  body: {
    backgroundColor: '#FFFFFF',
  },
  appbar: {
    backgroundColor: '#FFFFFF',
    color: '#111111',
  },
});

function App() {
  return (
    <React.Fragment>
      <ThemeProvider theme={theme}>
        <AppBar/>
      </ThemeProvider>
    </React.Fragment>
  );
}

export default App;
